<template>
  <nav ref="map" class="map">
    <button @click="arrowMap" class="map__arrow">
      <span ref="mapBtn"></span>
    </button>
    <h4 class="map-title">Панель навигации</h4>
    <ul class="map-list">
      <li v-for="item in items" :key="item.id" class="map-item">
        <router-link @click="closeMap" :to="{ path: item.links }">{{ item.name }}</router-link>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      items: [
        { id: 1, name: "Мультфильмы 3d", links: "/films" },
        { id: 2, name: "Мультфильмы 2d", links: "/cartoons" },
      ],
    };
  },

  methods: {
    arrowMap() {
      this.$refs.mapBtn.classList.toggle("active");
      this.$refs.map.classList.toggle("open");
    },
    closeMap(){
       if(this.$refs.mapBtn.classList.contains("active")){
      this.$refs.mapBtn.classList.remove("active");
      this.$refs.map.classList.remove("open");
      }
    }
  },
};
</script>

<style lang='scss'>
@import "../assets/style/veribals.scss";
.map {
  background: $color2;
  color: $color4;
  padding: 20px 30px;
  min-height: 100vh;
  position: relative;
}
.map-title {
  font-size: 18px;
  display: block;
  white-space: nowrap;
}
.map-list {
  margin: 20px 0;
  list-style: none;
}
.map-item {
  cursor: pointer;
  margin: 10px 0;
  padding: 5px;
  border: 1px solid $color3;
  width: 100%;
  transition: all 0.3s;
  &:hover {
    background: $color3;
  }
  &:active {
    background: $color4;
  }
  a{
    color: $color4;
  }
  a:visited {
    color: $color4;
  }
}
@media (max-width: 768px) {
  .map {
    position: absolute;
    transition: all 0.3s;
    transform: translateX(-100%);
  }
  .map.open {
    transform: translateX(0%);
  }
  .map__arrow {
    width: 30px;
    height: 40px;
    background: $color2;
    position: absolute;
    right: -30px;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 10px;
    span {
      display: block;
      width: 20px;
      height: 20px;
      border: 0px solid #000;
      border-bottom: 10px solid transparent;
      border-top: 10px solid transparent;
      border-left: 10px solid $color4;
      transition: all 0.1s;
      &.active {
        transform: translateX(-50%) rotate(180deg);
      }
    }
  }
}
</style>