<template>
  <button @click="goto" class="btn_top">
    <span></span>
  </button>
</template>

<script>
export default {
  data() {
    return {
      wiewArrow: false,
    };
  },
  computed: {},
  methods: {
    goto() {
      scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
  mounted() {
    const arrow = document.querySelector(".btn_top");
    window.addEventListener("scroll", function () {
      if (window.pageYOffset > window.innerHeight / 2 && !this.wiewArrow) {
        this.wiewArrow = true;
        arrow.classList.add("active");
      } else if (
        window.pageYOffset < window.innerHeight / 2 &&
        this.wiewArrow
      ) {
        this.wiewArrow = false;
        arrow.classList.remove("active");
      }
    });
  },
};
</script>

<style lang="scss">
@import "../assets/style/veribals.scss";

.btn_top {
  display: block;
  width: 80px;
  height: 80px;
  position: fixed;
  bottom: 10%;
  right: 10%;
  background: $color1;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  animation: rotate 0.8s infinite alternate linear;
  transition: all 0.8s;
  opacity: 0;
  visibility: hidden;
  span {
    background: url(../assets/arrow.png) center / cover no-repeat;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    box-shadow: 2px 2px 5px $color4;
    transition: all 0.2s;
    &:hover {
      box-shadow: 2px 3px 15px $color4;
    }
    &:active {
      transition: all 0s;
      box-shadow: 2px 2px 10px $color4;
    }
  }
}
.btn_top.active {
  opacity: 1;
  visibility: visible;
}

@media (max-width: 568px) {
  .btn_top {
    width: 70px;
    height: 70px;
    span {
      width: 55px;
      height: 55px;
    }
  }
}
@keyframes rotate {
  0% {
    transform: rotate(-12deg);
  }
  100% {
    transform: rotate(12deg);
  }
}
</style>