<template>
  <header class="header">
    <div class="container">
      <div class="header__row">
        <a href="#" class="logo"> kinoTIME </a>
        <nav class="menu">
          <ul ref="menu" class="menu__list">
            <li v-for="item in menuItems" :key="item.id" class="menu__item">
              <router-link @click="closeMenu" :to="{ path: item.links }">{{
                item.name
              }}</router-link>
            </li>
          </ul>
          <button ref="menuBtn" @click="navBurger" class="menu__icon">
            <span></span>
          </button>
        </nav>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  data() {
    return {
      menuItems: [
        { id: 1, name: "Home", links: "/" },
        { id: 2, name: "Films", links: "/Cartoons" },
        { id: 3, name: "Setings", links: "/setings" },
      ],
    };
  },
  methods: {
    navBurger() {
      this.$refs.menu.classList.toggle("open");
      this.$refs.menuBtn.classList.toggle("active");
    },
    closeMenu() {
      if (this.$refs.menuBtn.classList.contains("active")) {
        this.$refs.menu.classList.remove("open");
        this.$refs.menuBtn.classList.remove("active");
      }
    },
  },
};
</script>

<style lang='scss'>
@import "../assets/style/veribals.scss";

.header {
  background: $color4;
  font-size: 20px;
}
.menu {
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.menu__item a {
  display: block;
  padding: 10px 30px;
  color: $color2;
  transition: all 0.2s;
  &:hover {
    background: $color3;
  }
  &:active {
    background: $color4;
  }
}
.header__row {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.logo {
  color: $color1;
  margin-left: 50px;
  padding: 10px 0;
  position: relative;
  z-index: 2;
}
.menu__icon {
  display: none;
  background: transparent;
}
.menu__list {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
@media (max-width: 768px) {
  .logo {
    margin-left: 20px;
  }
  .menu__list {
    padding-top: 80px;
    flex-direction: column;
    position: fixed;
    justify-content: flex-start;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: #1f4e5fd9;
    transition: all 0.6s;
    transform: translate(100%);
    &.open {
      transform: translate(0%);
    }
  }
  .menu__icon {
    padding: 10px;
    z-index: 1;
    display: block;
    margin: 0 15px;
    width: 30px;
    height: 20px;
    position: relative;
    span,
    &::before,
    &::after {
      content: "";
      position: absolute;
      height: 10%;
      width: 100%;
      left: 0;
      background: $color3;
      transition: all 0.6s;
    }
    span {
      top: 50%;
      transform: translate(0, -50%);
      transition: transform 0.6s ease 0.1s, opacity 0.6s;
    }
    &::before {
      top: 0%;
    }
    &::after {
      bottom: 0%;
    }
    &.active {
      span {
        transform: translateX(-150%);
        opacity: 0;
        transition: transform 0.6s, opacity 0.6s ease 0.3s;
      }
      &::before {
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
      }
      &::after {
        bottom: 50%;
        transform: translateY(50%) rotate(-45deg);
      }
    }
  }
}
</style>