<template>
  <div class="icon" v-for="icon in data" :key="icon.id">
    <div class="icon__numb">{{ icon.id }}</div>
    <h4 class="icon__title">{{ icon.title }}</h4>
    <div class="icon__row">
      <div class="icon__row-item-img">
        <div class="icon__img"><img :src="icon.image" :alt="img" /></div>
      </div>
      <div class="icon__row-item-body">
        <div class="sub">автор:</div>
        <div v-for="item in icon.creator" class="value">
          <div>{{ item }}</div>
        </div>
        <div class="sub">Количество эпизодов:</div>
        <div class="value">{{ icon.episodes }}</div>
        <div class="sub">Год выпуска:</div>
        <div class="value">{{ icon.year }}</div>
        <div class="sub">Жанр:</div>
        <div v-for="item in icon.genre" class="value">{{ item }}</div>
        <div class="sub">Длительность:</div>
        <div class="value">{{ icon.runtime_in_minutes }} min</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: [],
    };
  },
  mounted() {
    fetch("https://api.sampleapis.com/cartoons/cartoons2D")
      .then((rest) => rest.json())
      .then((data) => {
        this.data = data;
      });
  },
};
</script>

<style lang='scss'>
@import "../assets/style/veribals.scss";
.icon {
  max-width: 400px;
  font-size: 16px;
  color: $color4;
  margin: 10px auto;
  border: 1px solid $color4;
  border-radius: 10px;
  padding:  10px;
  background: $color1;
  flex: 1 1 100%;
  transition: all 0.3s;
  &:hover {
    box-shadow: 2px 2px 15px $color4;
  }
}
.icon__numb {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: $color4;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px auto;
}
.icon__title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 10px;
}
.icon__img {
  max-width: 200px;
  margin-right: 5px;
  border-radius: 15px;
  overflow: hidden;
  img {
    width: 100%;
  }
  margin-bottom: 10px;
}
.icon__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1 1 100%;
}
.icon__row-item-body{
   flex:1 1 auto;
}
.sub {
  font-size: 14px;
  color: $color3;
  margin-bottom: 10px;
}
.value {
  font-size: 16px;
  color: $color4;
  margin-bottom: 10px;
}
</style>